<template>
    <div id="linkman">
      <!--标题栏 -->
      <div id="title">
        <mt-header title="最近联系人">
          <router-link to="/head" slot="left">
            <mt-button icon="back">返回</mt-button>
          </router-link>
        </mt-header>
      </div>
      <!--联系人列表-->
      <div id="linkmanList">
        <mt-loadmore  :bottom-method="loadBottom" :bottomPullText='bottomText'
                      :bottom-all-loaded="allLoaded" ref="loadmore">
          <div  v-for="(item,index) in list" :key="index">
            <mt-cell class="cell" :title="item.name"  :to="item.url"  is-link  ></mt-cell>
          </div>
        </mt-loadmore>
      </div>

    </div>
</template>

<script>
    export default {
        name: "linkman",
      data(){
        return{
          bottomText: '上拉加载更多...',
          allLoaded:false,
          pageNo:1,
          pageSize:10,
          totalPage:100,
          list:[
            {
              name:"王东",
              url:"/talk?id=2348"
            },
          ],
        }
      },
      methods: {
        loadBottom: function () {
          setTimeout(console.log("加载更多数据"), 2000);
          var x = {
              name:"王东",
              url:"/talk?id=2348"
            };
          this.list.push(x)
          //this.allLoaded = true;// 若数据已全部获取完毕
          this.$refs.loadmore.onBottomLoaded();
        },
        more: function () {
          // 分页查询
          if (this.totalPage == 1) {
            this.pageNo = 1;
            this.allLoaded = true;
          } else {
            this.pageNo = this.pageNo + 1;
            this.allLoaded = false;
          }
        },
        isHaveMore:function(){
          // 是否还有下一页，如果没有就禁止上拉刷新
          //this.allLoaded = false; //true是禁止上拉加载
          if(this.pageNo == this.totalpage){
            this.allLoaded = true;
          }
        },
      },

    }
</script>

<style scoped>

</style>



