<template>
    <div id="drugMerchants">
      <div id="title">
        <mt-header title="招商空白点">
          <router-link to="/drugInfo" slot="left">
            <mt-button icon="back">返回</mt-button>
          </router-link>
        </mt-header>
      </div>

      <div style="width: 100%">
        <table style="width: 100%;"  cellspacing="0" cellpadding="0">
          <thead >
           <tr style="height: 1rem;background: gainsboro;">
             <th>省份</th>
             <th>地级市</th>
             <th>建议零售价</th>
             <th>是否招商</th>
           </tr>
          </thead>
          <tbody>
            <tr v-for="(item,index) in list" :key="index" style="border: 1px solid #808080;height: 1rem;">
              <td>{{item.province}}</td>
              <td>{{item.city}}</td>
              <td>{{item.price}}</td>
              <td v-if="item.isMerchants==0"><div style="margin:0 auto;width:70%;height: 0.6rem;background: green;color: white;border-radius: 0.1rem;line-height: 0.6rem">可招商</div></td>
              <td v-else><div style="margin:0 auto;width:70%;height: 0.6rem;background: red;color: white;border-radius: 0.1rem;line-height: 0.6rem">已招商</div></td>
            </tr>
          </tbody>
        </table>
      </div>

    </div>
</template>

<script>
    export default {
        name: "drugMerchants",
      data(){
        return{
          list:[
            {
              province:"北京",
              city:"东城区",
              price:"0",
              isMerchants:0,
            },
            {
              province:"北京",
              city:"东城区",
              price:"0",
              isMerchants:1,
            }
          ],
        }
      },
    }
</script>

<style scoped>
  table,table tr th, table tr td { border:1px solid #808080; }
</style>



