<template>
  <div id="Merchants">
    <div id="title">
      <mt-header title="药品列表">
      </mt-header>
    </div>

    <div style="overflow: scroll;">
      <!--药品列表，下拉加载更多-->
      <mt-loadmore  :bottom-method="loadBottom" :bottomPullText='bottomText'
                    :bottom-all-loaded="allLoaded" ref="loadmore">
        <ul style="list-style: none;padding: 0;">
          <li v-for="(item,index) in list" :key="index">
            <div style="width:100%;height:1.6rem;position: relative;margin-bottom: 0.1rem;border-bottom: 1px solid #e6e6e6" @click="()=>{$router.push({path: '/drugInfo?id='+item.id});}">
              <img style="width: 1.2rem;height: 1.2rem;
              position: absolute;left:0.5rem;top: 50%;margin-top: -0.6rem;" :src="item.drugImg"/>
              <p style="display: inline-block;position: absolute;left:2rem;top:0.4rem;margin: 0;font-size: 14px;">{{item.drugName}}</p>
              <p style="display: inline-block;position: absolute;left:2rem;top:0.9rem;margin: 0;color: #808080;">{{item.drugIntro}}</p>
            </div>
          </li>
        </ul>
      </mt-loadmore>
    </div>
    <mt-tabbar v-model="selected" :fixed="true">
      <mt-tab-item id="index">
        <img slot="icon" src="../../../../assets/index.png">
        首页
      </mt-tab-item>
      <mt-tab-item id="head">
        <img slot="icon" src="../../../../assets/head.png">
        总部
      </mt-tab-item>
      <mt-tab-item id="merchants">
        <img slot="icon" src="../../../../assets/merchants.png">
        招商
      </mt-tab-item>
      <mt-tab-item id="mine">
        <img slot="icon" src="../../../../assets/mine.png">
        我的
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
    export default {
        name: "Merchants",
      data(){
          return{
            selected:"merchants",
            bottomText: '上拉加载更多...',
            allLoaded:false,
            list:[
               {
                 drugImg:"http://img.zcool.cn/community/01112a56c3c4496ac7256cb0d935d6.jpg",
                 drugName:"盐酸蜜拉普片",
                 drugIntro:"1片*10个"
               },
              {
                drugImg:"http://img.zcool.cn/community/01112a56c3c4496ac7256cb0d935d6.jpg",
                drugName:"盐酸蜜拉普片",
                drugIntro:"1片*10个"
              },
              {
                drugImg:"http://img.zcool.cn/community/01112a56c3c4496ac7256cb0d935d6.jpg",
                drugName:"盐酸蜜拉普片",
                drugIntro:"1片*10个"
              },
            ],
            pageNo:1,
            pageSize:10,
            totalPage:100,
          }
      },
      methods: {
        loadBottom: function () {
          setTimeout(console.log("加载更多数据"), 2000);
          var x = {
            drugImg:"http://img.zcool.cn/community/01112a56c3c4496ac7256cb0d935d6.jpg",
            drugName:"盐酸蜜拉普片",
            drugIntro:"1片*10个"
          };
          this.list.push(x)
          //this.allLoaded = true;// 若数据已全部获取完毕
          this.$refs.loadmore.onBottomLoaded();
        },
        more: function () {
          // 分页查询
          if (this.totalPage == 1) {
            this.pageNo = 1;
            this.allLoaded = true;
          } else {
            this.pageNo = this.pageNo + 1;
            this.allLoaded = false;
          }
        },
        isHaveMore:function(){
          // 是否还有下一页，如果没有就禁止上拉刷新
          //this.allLoaded = false; //true是禁止上拉加载
          if(this.pageNo == this.totalpage){
            this.allLoaded = true;
          }
        },
      },
      watch:{
        selected:function (newVal,oldVal) {
          console.log("val======"+newVal);
          console.log("oldval======"+oldVal);
          // 这里就可以通过 val 的值变更来确定去向
          switch(newVal){
            case 'index':
              this.$router.push('/index');
              break;
            case 'head':
              this.$router.push('/head');
              break;
            case 'merchants':
              this.$router.push('/merchants');
              break;
            case 'mine':
              this.$router.push('/mine');
              break;
          }
        }
      },
    }
</script>

<style scoped>

</style>



