<template>
    <div id="QueryCustomer">
      <!--标题栏 -->
      <div id="title">
        <mt-header title="客户查询">
          <router-link to="/head" slot="left">
            <mt-button icon="back">返回</mt-button>
          </router-link>
        </mt-header>
      </div>

      <div id="query">
        <mt-search style="height:100% !important;"
          v-model="value"
          cancel-text="取消"
          placeholder="搜索">
        </mt-search>
        <div @click='()=>{this.isShowLevel="none";this.isShowPlace="inline-block";}'>
          <mt-cell title="地域筛选" ><span style="color: #808080">{{selectText1}} ></span></mt-cell>
        </div>
        <div  @click='()=>{this.isShowPlace="none";this.isShowLevel="inline-block"}'>
          <mt-cell title="级别筛选" ><span style="color: #808080">{{selectText2}} ></span></mt-cell>
        </div>

      </div>

      <!--地域筛选-->
      <div id="place" style="position: fixed;bottom: 0;left: 0;z-index: 100;width: 100%" :style="{display:isShowPlace}">
        <div style="width: 100%;height:1rem;background: #104E8B">
          <span style="height: 1rem;width: 1.5rem;line-height: 1rem;float: left;color: white" @click='()=>{this.isShowPlace="none"}'>取消</span>
          <span style="height: 1rem;width: 1.5rem;line-height: 1rem;float: right;color: white" @click='()=>{this.isShowPlace="none"}'>完成</span>
          <div style="clear: both"></div>
        </div>
        <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
      </div>

      <!--级别筛选-->
      <div id="level" @click='()=>{this.isShowLevel="none"}' style="position: fixed;bottom: 0;left: 0;z-index: 100;width: 100%;background: #104E8B;padding: 0.3rem;box-sizing: border-box" :style="{display:isShowLevel}">
        <p style="color: white;text-align: left;">级别</p>
        <p style="float:left;display:inline-block;width: 1.5rem ;height:0.6rem;background: white;line-height: 1rem;color: #104e8b;border-radius: 0.5rem;line-height: 0.6rem;margin-right: 0.3rem"
           v-for="(item,index) in levels" :key="index" @click.stop='()=>{this.isShowLevel="none"}' >{{item}}</p>
      </div>

      <!--地域或者级别列表-->
      <div id="showPlace" >
        <div v-for="(item,index) in list" :key="index" style="border-bottom: 1px solid #e6e6e6" @click="()=>{$router.push({path: '/customerInfo?id='+item.id});}" >
          <mt-cell :title="item.name" ></mt-cell>
        </div>
      </div>


    </div>
</template>

<script>
    export default {
        name: "QueryCustomer",
      data(){
        return{
          levels:['经销商','分销商','代理商'],
          isShowLevel:"none",
          isShowPlace:"none",
          selectText1:"北京大区 东北",
          selectText2:"代理商",
          value:'',
          list:[
            {
              name:"张安乐",
              id:2546
            },
            {
              name:"张安乐",
              id:2546
            },
          ],
          slots: [
            {
              flex: 1,
              values: ['华北大区', '华南大区', '华东一区'],
              className: 'slot1',
              textAlign: 'right'
            }, {
              divider: true,
              content: '-',
              className: 'slot2'
            }, {
              flex: 1,
              values: ['广东', '北京', '天津', '上海', '南京', '海南'],
              className: 'slot3',
              textAlign: 'left'
            }
          ]
        }
      },
      methods: {
        onValuesChange(picker, values) {
          if (values[0] > values[1]) {
            picker.setSlotValue(1, values[0]);
          }
        }
      },
    }
</script>

<style scoped>

</style>



