<template>
  <div id="Mine">
    <div id="title">
      <mt-header title="我的">
      </mt-header>
    </div>
    <!--头像-->
    <div class="img">
       <img style="width: 2rem;height:2rem;border-radius: 2rem;margin-top: 1rem;border: 2px solid white" :src="customerImg"/>
      <p style="color: white">{{customerName}}</p>
    </div>

    <!--账户 密码  关于现代-->
    <div class="account">
      <mt-cell class="cell" title="账户密码"  to="/updatePassword"  is-link  value="修改"></mt-cell>
      <mt-cell class="cell" title="关于现代"  to="/aboutModern"  is-link  ></mt-cell>
    </div>

    <!--退出登录-->
    <div style="margin-top: 0.5rem;">
      <mt-button type="primary" size="large" >退出登录</mt-button>
    </div>

    <mt-tabbar v-model="selected" :fixed="true">
      <mt-tab-item id="index">
        <img slot="icon" src="../../../../assets/index.png">
        首页
      </mt-tab-item>
      <mt-tab-item id="head">
        <img slot="icon" src="../../../../assets/head.png">
        总部
      </mt-tab-item>
      <mt-tab-item id="merchants">
        <img slot="icon" src="../../../../assets/merchants.png">
        招商
      </mt-tab-item>
      <mt-tab-item id="mine">
        <img slot="icon" src="../../../../assets/mine.png">
        我的
      </mt-tab-item>
    </mt-tabbar>

  </div>
</template>

<script>
    export default {
        name: "Mine",
      data(){
          return{
            customerImg:"http://img.zcool.cn/community/01112a56c3c4496ac7256cb0d935d6.jpg",
            customerName:"总部4",
            selected:"mine",
          }
      },
      watch:{
        selected:function (newVal,oldVal) {
          console.log("val======"+newVal);
          console.log("oldval======"+oldVal);
          // 这里就可以通过 val 的值变更来确定去向
          switch(newVal){
            case 'index':
              this.$router.push('/index');
              break;
            case 'head':
              this.$router.push('/head');
              break;
            case 'merchants':
              this.$router.push('/merchants');
              break;
            case 'mine':
              this.$router.push('/mine');
              break;
          }
        }
      },
    }
</script>

<style scoped>
.img{
  width: 100%;
  height:5rem;
  background: url("http://img3.imgtn.bdimg.com/it/u=2354746594,25823525&fm=26&gp=0.jpg");
}

.account{
  margin-top: 0.4rem;
}

.cell{
  margin-top: 0.4rem;
  background: white;
  border-bottom: 1px solid #e6e6e6 ;
  border-top: 1px solid #e6e6e6 ;
}

.mint-button--primary{
  background: #66CDAA;
}
</style>



