import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/pages/Home'
import Index from '@/components/pages/childPages/index/Index'
import Head from '@/components/pages/childPages/head/Head'
import Merchants from '@/components/pages/childPages/merchants/Merchants'
import Mine from '@/components/pages/childPages/mine/Mine'
import LinkMan from '@/components/pages/childPages/head/linkman'
import QueryCustomer from '@/components/pages/childPages/head/QueryCustomer'
import LevelMessage from '@/components/pages/childPages/head/levelMessage'
import LevelMessageContent from '@/components/pages/childPages/head/levelMessageContent'
import Events from '@/components/pages/childPages/head/events'
import EventInfo from '@/components/pages/childPages/head/eventInfo'
import DrugInfo from '@/components/pages/childPages/merchants/drugInfo'
import DrugMerchants from '@/components/pages/childPages/merchants/drugMerchants'
import UpdatePassword from '@/components/pages/childPages/mine/updatePassword'
import AboutModern from '@/components/pages/childPages/mine/aboutModern'
import CustomerInfo from '@/components/pages/childPages/head/CustomerInfo'

Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/index',
      name: 'Index',
      component: Index,
    },
    {
      path: '/merchants',
      name: 'Merchants',
      component: Merchants,
    },
    {
      path: '/head',
      name: 'Head',
      component: Head,
    },
    {
      path: '/mine',
      name: 'Mine',
      component: Mine,
    },
    // {
    //   path: '/home',
    //   name: 'Home',
    //   component: Home,
    //   children:[
    //     {
    //       path:'/index',
    //       component:Index
    //     },
    //     {
    //       path:'head',
    //       component:Head
    //     },
    //     {
    //       path:'merchants',
    //       component:Merchants
    //     },
    //     {
    //       path:'mine',
    //       component:Mine
    //     },
    //   ]
    // },
    {
      path: '/hello',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/linkMan',
      name: 'LinkMan',
      component: LinkMan
    },
    {
      path: '/queryCustomer',
      name: 'QueryCustomer',
      component: QueryCustomer
    },
    {
      path: '/levelMessage',
      name: 'LevelMessage',
      component: LevelMessage
    },
    {
      path: '/levelMessageContent',
      name: 'LevelMessageContent',
      component: LevelMessageContent
    },
    {
      path: '/events',
      name: 'Events',
      component: Events
    },
    {
      path: '/eventInfo',
      name: 'EventInfo',
      component: EventInfo
    },
    {
      path: '/drugInfo',
      name: 'DrugInfo',
      component: DrugInfo
    },
    {
      path: '/drugMerchants',
      name: 'DrugMerchants',
      component: DrugMerchants
    },
    {
      path: '/updatePassword',
      name: 'UpdatePassword',
      component: UpdatePassword
    },
    {
      path: '/aboutModern',
      name: 'AboutModern',
      component: AboutModern
    },
    {
      path: '/customerInfo',
      name: 'CustomerInfo',
      component: CustomerInfo
    },

  ]
})



