<template>
  <div id="Head">
    <!--标题栏 -->
    <div id="title">
       <mt-header title="总部">
       </mt-header>
     </div>

    <mt-cell class="cell" title="最近联系人"  to="/linkMan"  is-link  ></mt-cell>
    <mt-cell class="cell"  title="客户查询"  to="/queryCustomer"  is-link ></mt-cell>
    <mt-cell class="cell"  title="客户留言"  to="/levelMessage"  is-link  ></mt-cell>
    <mt-cell class="cell"  title="活动信息"  to="/events"  is-link  ></mt-cell>

    <mt-tabbar v-model="selected" :fixed="true">
      <mt-tab-item id="index">
        <img slot="icon" src="../../../../assets/index.png">
        首页
      </mt-tab-item>
      <mt-tab-item id="head">
        <img slot="icon" src="../../../../assets/head.png">
        总部
      </mt-tab-item>
      <mt-tab-item id="merchants">
        <img slot="icon" src="../../../../assets/merchants.png">
        招商
      </mt-tab-item>
      <mt-tab-item id="mine">
        <img slot="icon" src="../../../../assets/mine.png">
        我的
      </mt-tab-item>
    </mt-tabbar>


  </div>
</template>

<script>
    export default {
        name: "Head",
      data(){
          return{
            selected:"head",
          }
      },
      watch:{
        selected:function (newVal,oldVal) {
          console.log("val======"+newVal);
          console.log("oldval======"+oldVal);
          // 这里就可以通过 val 的值变更来确定去向
          switch(newVal){
            case 'index':
              this.$router.push('/index');
              break;
            case 'head':
              this.$router.push('/head');
              break;
            case 'merchants':
              this.$router.push('/merchants');
              break;
            case 'mine':
              this.$router.push('/mine');
              break;
          }
        }
      },
    }
</script>

<style scoped>

.cell{
  background: white;
  border-bottom: 1px solid #e6e6e6 ;
}

  .mint-cell-title{
    text-align: left !important;
  }


</style>



