<template>
    <div id="drugInfo">
      <div id="title">
        <mt-header :title="drugName">
          <router-link to="/merchants" slot="left">
            <mt-button icon="back">返回</mt-button>
          </router-link>
        </mt-header>
      </div>

      <p>{{drugName}}/{{drugSpec}}</p>
      <div style="margin-top: 0.5rem;">
        <mt-button type="primary" size="large" style="background: #104e8b;width: 90%;margin: 0 auto" @click="()=>{$router.push({path: '/drugMerchants?id='+drugId});}" >查看招商空白点</mt-button>
      </div>
    </div>
</template>

<script>
    export default {
        name: "drugInfo",
      data(){
          return{
            drugName:"感冒药",
            drugId:255,
            drugSpec:"2袋每盒"
          }
      }
    }
</script>

<style scoped>

</style>



