<template>
    <div id="levelMessage">
      <!--标题栏 -->
      <div id="title">
        <mt-header title="客户留言">
          <router-link to="/head" slot="left">
            <mt-button icon="back">返回</mt-button>
          </router-link>
        </mt-header>
      </div>


      <mt-loadmore  :bottom-method="loadBottom" :bottomPullText='bottomText'
                    :bottom-all-loaded="allLoaded" ref="loadmore">
        <div v-for="(item,index) in list" :key="index" style="border-bottom: 1px solid #e6e6e6" >
          <mt-cell :title="item.name" :label="item.time" :to="item.url" is-link></mt-cell>
        </div>
      </mt-loadmore>



    </div>
</template>

<script>
    export default {
        name: "levelMessage",
      data(){
        return{
          bottomText: '上拉加载更多...',
          allLoaded:false,
          list:[
            {
              time:"2018-25-30 22:27:28",
              name:"游客1",
              id:3157,
              url:"/levelMessageContent?id=3157",
            }
          ],
          pageNo:1,
          pageSize:10,
          totalPage:100,
        }
      },
      methods: {
        loadBottom: function () {
          setTimeout(console.log("加载更多数据"), 2000);
          var x = {
            time:"2018-25-30 22:27:28",
            name:"游客1",
            id:3157,
            url:"/levelMessageContent?id=3157",
          };
          this.list.push(x)
          //this.allLoaded = true;// 若数据已全部获取完毕
          this.$refs.loadmore.onBottomLoaded();
        },
        more: function () {
          // 分页查询
          if (this.totalPage == 1) {
            this.pageNo = 1;
            this.allLoaded = true;
          } else {
            this.pageNo = this.pageNo + 1;
            this.allLoaded = false;
          }
        },
        isHaveMore:function(){
          // 是否还有下一页，如果没有就禁止上拉刷新
          //this.allLoaded = false; //true是禁止上拉加载
          if(this.pageNo == this.totalpage){
            this.allLoaded = true;
          }
        },


      }
    }
</script>

<style scoped>

</style>



