<template>
    <div id="events">
      <div id="title">
        <mt-header title="活动信息">
          <router-link to="/head" slot="left">
            <mt-button icon="back">返回</mt-button>
          </router-link>
        </mt-header>
      </div>


      <mt-loadmore  :bottom-method="loadBottom" :bottomPullText='bottomText'
                    :bottom-all-loaded="allLoaded" ref="loadmore">
        <ul style="list-style: none;padding: 0;border-bottom: 1px solid #e6e6e6">
          <li v-for="(item,index) in list" :key="index">
            <div style="width:100%;height:1.6rem;position: relative;margin-bottom: 0.1rem;border-top: 1px solid #e6e6e6" @click="()=>{$router.push({path: '/eventInfo?id='+item.id});}">
              <img style="width: 1.2rem;height: 1.2rem;
                          position: absolute;left:0.5rem;top: 50%;margin-top: -0.55rem;" :src="item.img"/>
              <p style="display: inline-block;position: absolute;left:2rem;margin: 0;font-size: 14px;height:1.6rem;line-height: 1.7rem;">{{item.title}}</p>
            </div>
          </li>
        </ul>
      </mt-loadmore>




    </div>
</template>

<script>
    export default {
        name: "events",
      data(){
          return{
            bottomText: '上拉加载更多...',
            allLoaded:false,
            list:[
              {
                img:"http://img.zcool.cn/community/01112a56c3c4496ac7256cb0d935d6.jpg",
                title:"广州省医药学会第十六次精神医学",
                id:2547
              },
              {
                img:"http://img.zcool.cn/community/01112a56c3c4496ac7256cb0d935d6.jpg",
                title:"广州省医药学会第十六次精神医学",
                id:2547
              },
            ]
          }
      }
    }
</script>

<style scoped>

</style>



